export const getAllRoles = () => {
	const app = JSON.parse(localStorage.getItem('app') || '{}');
	const { roles = [] } = app;

	return roles;
};

export const getRoleById = (functId = '') => {
	const app = JSON.parse(localStorage.getItem('app') || '{}');
	const { roles = [] } = app;

	const filteredRole = roles?.filter((rl) => rl.functId === functId);

	if (filteredRole.length !== 0) {
		return filteredRole[0];
	}

	return {};
};
