import { LoadingOutlined, SearchOutlined } from '@ant-design/icons';
import { Button, Col, Form, Input, notification, Row, Space, Table, Tag } from 'antd';
import moment from 'moment';
import React, { useEffect, useState } from 'react';
import Highlighter from 'react-highlight-words';
import { HiPlusCircle, HiSearch } from 'react-icons/hi';
import { useDispatch, useSelector } from 'react-redux';
import { useHistory } from 'react-router-dom';
import { useReactToPrint } from 'react-to-print';
import actions from '../../../redux/transaction/actions';
import { getRoleById } from '../../../utils/helper/roleAccess';
import { currencyFormat } from '../../../utils/helper/utilities';
import { PrintPage } from '../PrintPage';

export function OrderHistory() {
	const history = useHistory();

	let componentPrintRef = React.createRef();
	let refInput = React.createRef();
	const isAccessToRefund = Object.keys(getRoleById('1032')).length !== 0;
	const { property = {} } = JSON.parse(localStorage.getItem('app') || '{}');

	const dispatch = useDispatch();

	const { historyTransactionList, historyTransactionDetail: detail, loadingDetail, loadingList } = useSelector((state) => state?.transaction);

	const [state, setState] = useState({
		modalReturn: false,
		date: moment(property?.refDate).format('YYYY-MM-DD'),
		dateTo: moment(property?.refDate).format('YYYY-MM-DD'),
		searchText: '',
		searchedColumn: '',
		custCode: ''
	});

	const getColumnSearchProps = (dataIndex, name = '') => ({
		filterDropdown: ({ setSelectedKeys, selectedKeys, confirm, clearFilters }) => (
			<div style={{ padding: 8 }}>
				<Input
					ref={refInput}
					placeholder={`Search ${name}`}
					value={selectedKeys[0]}
					onChange={(e) => setSelectedKeys(e.target.value ? [e.target.value] : [])}
					onPressEnter={() => handleSearch(selectedKeys, confirm, dataIndex)}
					style={{ marginBottom: 8, display: 'block' }}
				/>
				<Space>
					<Button
						type="primary"
						onClick={() => handleSearch(selectedKeys, confirm, dataIndex)}
						icon={<SearchOutlined />}
						size="small"
						style={{ width: 90 }}>
						Search
					</Button>
					<Button onClick={() => handleReset(clearFilters)} size="small" style={{ width: 90 }}>
						Reset
					</Button>
				</Space>
			</div>
		),
		filterIcon: (filtered) => <SearchOutlined style={{ color: filtered ? '#1890ff' : undefined }} />,
		onFilter: (value, record) => (record[dataIndex] ? record[dataIndex].toString().toLowerCase().includes(value.toLowerCase()) : ''),
		onFilterDropdownVisibleChange: (visible) => {
			if (visible) {
				setTimeout(() => refInput.current.select(), 100);
			}
		},
		render: (text) =>
			// eslint-disable-next-line react/destructuring-assignment
			state.searchedColumn === dataIndex ? (
				<Highlighter
					highlightStyle={{ backgroundColor: '#ffc069', padding: 0 }}
					/* eslint-disable-next-line react/destructuring-assignment */
					searchWords={[state.searchText]}
					autoEscape
					textToHighlight={text ? text.toString() : ''}
				/>
			) : (
				text
			)
	});

	const handleSearch = (selectedKeys, confirm, dataIndex) => {
		confirm();
		setState({
			...state,
			searchText: selectedKeys[0],
			searchedColumn: dataIndex
		});
	};

	const handleReset = (clearFilters) => {
		clearFilters();
		setState({ ...state, searchText: '' });
	};

	const handleSearchData = () => {
		const { date, dateTo } = state;

		dispatch({
			type: actions.TRANSACTION_HISTORY_LIST,
			payload: {
				dateFrom: date,
				to: dateTo
			}
		});

		dispatch({
			type: actions.SET_STATE,
			payload: {
				historyTransactionDetail: {}
			}
		});
	};

	const columns = [
		{
			title: 'No',
			key: 'no',
			dataIndex: 'no',
			width: 50,
			align: 'center'
		},
		{
			title: 'Tanggal',
			key: 'billDate',
			dataIndex: 'billDate',
			width: 200,
			align: 'center'
		},
		{
			title: 'Bill No',
			key: 'billNo',
			dataIndex: 'billNo',
			width: 300,
			align: 'left',
			...getColumnSearchProps('billNo', 'No Bill')
		},
		{
			title: 'Sub Total',
			key: 'subTotal',
			dataIndex: 'subTotal',
			width: 200,
			align: 'left',
			render: (t) => (
				<div className="w-full flex items-center gap-3">
					<p>Rp {currencyFormat(t, 0)}</p>
					{loadingDetail && <LoadingOutlined spin className="text-lg" />}
				</div>
			)
		},
		{
			title: 'Status',
			key: 'voidBill',
			dataIndex: 'voidBill',
			align: 'center',
			width: 150,
			render: (t) => (
				<div>
					{t === 1 && <Tag color="red">VOID</Tag>}
					{t === 0 && <Tag color="green">CLOSED</Tag>}
				</div>
			)
		},
		{
			title: 'Upload',
			key: 'posting',
			dataIndex: 'posting',
			align: 'center',
			width: 150,
			render: (t) => (
				<div>
					{+t === 0 && <Tag color="red">BELUM</Tag>}
					{+t === 1 && <Tag color="green">TERKIRIM</Tag>}
				</div>
			)
		},
		{
			title: 'Action',
			key: 'billNo',
			dataIndex: 'billNo',
			align: 'center',
			width: 150,
			render: (t) => (
				<Button
					disabled={!isAccessToRefund}
					type="primary"
					size="middle"
					className="rounded-md"
					onClick={() => {
						if (isAccessToRefund) {
							return handleReturnTrx(t);
						}

						return null;
					}}>
					Refund
				</Button>
			)
		}
	];

	const handleReturnTrx = (billNo) => {
		const { date, dateTo } = state;

		dispatch({
			type: actions.TRANSACTION_RETURN,
			payload: {
				billNo,
				dateFrom: date,
				to: dateTo
			}
		});
	};

	const onRow = (r) => ({
		onClick: () => {
			dispatch({
				type: actions.TRANSACTION_HISTORY_DETAIL,
				payload: {
					billNo: r.billNo
				}
			});
		}
	});

	const handlePrint = useReactToPrint({
		onPrintError: (e) => {
			console.log('Error Print!', e);

			notification.error({
				message: 'Print Struk!',
				description: `Gagal mencetak struk. ${e}`
			});
		},
		onAfterPrint: (e) => {
			notification.success({
				message: 'Print Struk!',
				description: 'Berhasil mencetak struk.'
			});
		}
	});

	const getTotalCashPay = () => {
		if (detail?.settle && detail?.settle.length !== 0) {
			const totalCashPay = detail?.settle.reduce((acc, cur) => {
				return +acc + +cur.cashPay;
			}, 0);

			return totalCashPay;
		}

		return 0;
	};

	useEffect(() => {
		dispatch({
			type: 'auth/property'
		});

		return () => {
			dispatch({
				type: actions.SET_STATE,
				payload: {
					historyTransactionList: [],
					historyTransactionDetail: []
				}
			});
		};
	}, []);

	return (
		<div className="w-full h-screen overflow-scroll">
			<div className="container mx-auto p-4">
				<div>
					<Form layout="vertical">
						<Row wrap gutter={[8, 8]} align="bottom">
							<Col span={8}>
								<Form.Item label="Dari Tanggal" name="date" initialValue={state?.date}>
									<Input
										type="date"
										onChange={(e) => setState({ ...state, date: e.target.value })}
										onPressEnter={handleSearchData}
										className="w-full h-14 rounded-md"
									/>
								</Form.Item>
							</Col>
							<Col span={8}>
								<Form.Item label="Sampai Tanggal" name="dateTo" initialValue={state?.dateTo}>
									<Input
										type="date"
										onChange={(e) => setState({ ...state, dateTo: e.target.value })}
										onPressEnter={handleSearchData}
										className="w-full h-14 rounded-md"
									/>
								</Form.Item>
							</Col>
							<Col span={5}>
								<Form.Item>
									<Button
										loading={loadingList}
										type="primary"
										onClick={handleSearchData}
										className="w-full h-14 flex items-center justify-center gap-3 rounded-md"
										icon={<HiSearch className="text-lg" />}>
										Cari Transaksi
									</Button>
								</Form.Item>
							</Col>
							<Col span={3}>
								<Form.Item>
									<Button
										className="w-full h-14 flex items-center justify-center gap-3 rounded-md"
										icon={<HiPlusCircle className="text-lg" />}
										onClick={() => history.push('/pos/kasir')}>
										Order Baru
									</Button>
								</Form.Item>
							</Col>
						</Row>
					</Form>
				</div>

				<Row className="mt-4" gutter={[25, 8]}>
					<Col span={17}>
						<Table
							size="large"
							columns={columns}
							dataSource={historyTransactionList.map((el, i) => ({ ...el, no: i + 1 }))}
							onRow={onRow}
							className="cursor-pointer hover:bg-yellow-200x transition-all ease-in-out"
							rowClassName={(record) => {
								if (record?.billNo === detail?.billNo) {
									return 'bg-yellow-300 hover:bg-yellow-200 transition-all ease-in-out';
								}

								return 'hover:bg-yellow-200 transition-all ease-in-out bg-white';
							}}
							pagination={{ defaultPageSize: 10, showSizeChanger: true }}
							loading={loadingList}
							scroll={{ x: columns.reduce((a, { width }) => a + +width, 0) }}
						/>
					</Col>
					<Col span={7}>
						{detail?.data?.length > 0 && (
							<button
								className="btn-page h-10 mb-3"
								onClick={(e) => handlePrint(e, () => componentPrintRef)}
								onKeyDown={(e) => {
									if (e.key === 'Enter') {
										handlePrint(e, () => componentPrintRef);
									}
								}}>
								PRINT STRUK <kbd className="kbd">enter</kbd>
							</button>
						)}
						<PrintPage
							ref={(el) => (componentPrintRef = el)}
							transactions={(detail?.data || [])?.map((item, index) => {
								return {
									...item,
									key: index + 1,
									ItemName: item?.menuName,
									total: item.qty * item.price
								};
							})}
							cash={{
								cashChange: (detail?.settle || [])[0]?.cashChange || '0',
								cashPaid: getTotalCashPay()
							}}
							billDate={(detail?.hdr || [])[0]?.billTime || new Date()}
							billNo={detail?.billNo}
							paymentMethod={(detail?.settle || [])[0]?.paidBy || ''}
							payments={(detail?.settle || []).map((settle) => ({ ...settle, tenderName: settle.paidBy }))}
							isReprintBill={true}
						/>
					</Col>
				</Row>
			</div>
		</div>
	);
}

export default OrderHistory;
